export function validateLogin (username, password) {
  if (!username) {
    return ''
  }
  if (!password) {
    return ''
  }
  return true
}

export function validateProfitInvest (money, dealPassword, selectAccount, code, agree) {
  if (!money) {
    return ''
  }
  if (!dealPassword) {
    return ''
  }
  if (selectAccount === '2') {
    if (!code) {
      return ''
    }
  }
  if (!agree) {
    return ''
  }
  return true
}

export function validateDeposit (money, dealPwd, code) {
  if (!money) {
    return ''
  }
  if (!dealPwd) {
    return ''
  }
  if (!code) {
    return ''
  }
  return true
}



